<template>
    <div class="container container-page">
        <div class="row justify-content-center">
            <div class="col-sm-3 mb-4 text-white">
                WOE date :
                <select class="form-control" v-model="selectedDate">
                    <option v-for="(date, index) in formattedWoeTimesWithResult" :key="index" :value="date">{{ date }}</option>
                </select>
            </div>
            <div class="col-md-12">
                <timelogs :viewingDate="preformattedSelectedDate"/>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import moment from 'moment'

import Timelogs from './Timelogs'

export default {
  name: 'PageTimelogs',
  components: {
    Timelogs
  },
  computed: {
    preformattedSelectedDate () {
      return moment(this.selectedDate, 'D MMMM YYYY').format('YYYYMMDD')
    },
    ...mapGetters([
      'formattedWoeTimesWithResult'
    ])
  },
  data () {
    return {
      selectedDate: ''
    }
  },
  updated () {
    if (this.formattedWoeTimesWithResult.length > 0) {
      if (this.selectedDate == '') {
        this.selectedDate = this.formattedWoeTimesWithResult[0]
      }
    }
  },
  mounted () {
    this.$forceUpdate()
  }
}
</script>



