<template>
    <div class="card text-center">
        <div class="card-header text-white bg-dark">
            ROMYSG Castle Standing as for {{ formattedSelectedDate }}
        </div>
        <div class="card-body p-0">
            <div class="table-responsive">
                <table class="table table-bordered table-sm">
                    <thead>
                    <tr class="">
                        <th scope="col">Castles</th>
                        <th scope="col">Inhabitants</th>
                        <th scope="col">Leader</th>
                        <th scope="col">Held for</th>
                        <th scope="col">Breaks</th>
                        <th scope="col"></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-if="isLoadingStandings">
                        <td colspan="6">
                            <i class="fa fa-spin fa-spinner"></i>
                        </td>
                    </tr>
                    <tr v-if="!isLoadingStandings && Object.keys(standingsByDateSelf).length === 0">
                        <td colspan="6">
                            <p class="mb-0">Data is still in progress. Stay tuned </p>
                        </td>
                    </tr>
                    <template v-for="( castles, town ) in standingsByDateSelf" v-if="!isLoadingStandings && Object.keys(standingsByDateSelf).length > 0">
                        <tr class="table-secondary" :key="town">
                            <th colspan="6">{{ town | capitalize }} Castles</th>
                        </tr>
                        <tr v-for="( castle ) in castles" :key="castle.name">
                            <th scope="row">{{ castle.name }}</th>
                            <td class="text-left">
                                <img :src="castle.inhabitants.emblem" alt=""> {{ castle.inhabitants.name }}
                            </td>
                            <td>
                                {{ castle.inhabitants.leader }}
                            </td>
                            <td>{{ castle.heldFor }} day<span v-if="castle.heldFor > 1">s</span></td>
                            <td>{{ castle.breaks }}</td>
                            <td>
                                <a href="#" class="btn btn-outline-info btn-sm" @click="detailsButtonClickHandler($event, castle.name)">Details</a>
                            </td>
                        </tr>
                    </template>
                    </tbody>
                </table>
            </div>
            <!--<h5 class="card-title">Special title treatment</h5>-->
            <!--<p class="card-text">With supporting text below as a natural lead-in to additional content.</p>-->
            <!--<a href="#" class="btn btn-primary">Go somewhere</a>-->
        </div>

        <div class="modal fade" id="modal-timelogs" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">{{ viewingTimelogCastle }}'s Timelogs</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <template v-if="isLoadingTimelog">
                            <p class="mb-0"><i class="fa fa-spin fa-spinner"></i> Crunching data. Please wait... </p>
                        </template>
                        <template v-if="!isLoadingTimelog && timelogsByCastleAndDateSelf.length === 0">
                            <p class="mb-0">Data for {{ viewingTimelogCastle }} is still in progress. Stay tuned </p>
                        </template>
                        <template v-if="!isLoadingTimelog && timelogsByCastleAndDateSelf.length > 0">
                            <div class="table-responsive">
                                <table class="table table-bordered table-sm mb-3">
                                    <tr>
                                        <th scope="row">WOE Date</th>
                                        <td>{{ formattedSelectedDate }}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Castle name</th>
                                        <td>{{ viewingTimelogCastle }}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Winner</th>
                                        <td>
                                            <img :src="timelogsByCastleAndDateSelf[timelogsByCastleAndDateSelf.length -1].inhabitant.emblem" alt="" v-if="timelogsByCastleAndDateSelf[timelogsByCastleAndDateSelf.length -1].inhabitant.emblem">
                                            {{ timelogsByCastleAndDateSelf[timelogsByCastleAndDateSelf.length -1].inhabitant.name }}</td>
                                    </tr>
                                </table>
                            </div>
                            <h5>Duration hold</h5>
                            <div class="progress mb-5 mt-5">
                                <template v-for="(row, index) in timelogsByCastleAndDateSelf">
                                    <div :class="['progress-bar', 'progress-bar-animated', 'progress-bar-striped', getBgColorByIndex(index) ]" role="progressbar" :style="{ width: row.percentage + '%'}">
                                        <div class="emblem-container" v-if="row.inhabitant.emblem">
                                            <img :src="row.inhabitant.emblem" alt="" class=""  data-toggle="tooltip" data-placement="bottom" :title="row.inhabitant.name">
                                        </div>
                                        {{ row.minutesHold }}m
                                    </div>
                                </template>

                                <!--<div class="progress-bar bg-success" role="progressbar" style="width: 30%" aria-valuenow="30" aria-valuemin="0" aria-valuemax="100"></div>-->
                                <!--<div class="progress-bar bg-info" role="progressbar" style="width: 20%" aria-valuenow="20" aria-valuemin="0" aria-valuemax="100"></div>-->
                            </div>

                            <h5>Timelogs</h5>
                            <div class="table-responsive">
                                <table class="table table-bordered table-sm">
                                    <thead>
                                    <tr>
                                        <th scope="col">Time</th>
                                        <th scope="col">Inhabitants</th>
                                        <th scope="col">Remarks</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <template v-for="row in timelogsByCastleAndDateSelf">
                                        <template v-if="row.inhabitant">
                                            <tr>
                                                <td>{{ row.time }}</td>
                                                <td class="text-left"><img :src="row.inhabitant.emblem" alt=""> {{ row.inhabitant.name }}</td>
                                                <td>{{ row.remarks }}</td>
                                            </tr>
                                        </template>
                                    </template>
                                    </tbody>
                                </table>
                            </div>
                        </template>
                    </div>
                    <div class="modal-footer">
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-sm-6 text-left">
                                    <button type="button" class="btn btn-secondary" @click="castleChangeClickHandler($event, castlesPaginatedListByNameSelf.prev)"
                                            v-shortkey="['arrowleft']" @shortkey="castleChangeClickHandler($event, castlesPaginatedListByNameSelf.prev)"
                                    >
                                        <i class="fa fa-arrow-left"></i> {{ castlesPaginatedListByNameSelf.prev }}
                                    </button>
                                </div>
                                <div class="col-sm-6 text-right">
                                    <button type="button" class="btn btn-secondary" @click="castleChangeClickHandler($event, castlesPaginatedListByNameSelf.next)"
                                            v-shortkey="['arrowright']" @shortkey="castleChangeClickHandler($event, castlesPaginatedListByNameSelf.next)"
                                    >
                                        {{ castlesPaginatedListByNameSelf.next }} <i class="fa fa-arrow-right"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import moment from 'moment'

export default {
  name: 'AllCastleStanding',
  data () {
    return {
      viewingTimelogCastle: '',
      isLoadingStandings: true,
      isLoadingTimelog: true
    }
  },
  props: [
    'viewingDate'
  ],
  watch: {
    viewingDate (newVal, oldVal) { // watch it
      this.isLoadingStandings = true
      this.$store.dispatch('getStandingsByDate', newVal).then(() => {
        this.isLoadingStandings = false
      }).catch(() => {
        this.isLoadingStandings = false
      })
    }
  },
  computed: {
    formattedSelectedDate () {
      return moment(this.viewingDate, 'YYYYMMDD').format('D MMMM YYYY')
    },
    timelogsByCastleAndDateSelf () {
      return this.timelogsByCastleAndDate(this.viewingTimelogCastle, this.viewingDate)
    },
    standingsByDateSelf () {
      return this.standingsByDate(this.viewingDate)
    },
    castlesPaginatedListByNameSelf () {
      return this.castlesPaginatedListByName(this.viewingTimelogCastle)
    },
    ...mapGetters([
      'standingsByDate',
      'timelogsByCastleAndDate',
      'castlesPaginatedListByName'
    ])
  },
  methods: {
    detailsButtonClickHandler (event, castleName) {
      event.preventDefault()

      this.viewingTimelogCastle = castleName
      this._modalTimelogs.modal()
    },
    initTimelogModal () {
      this.$store.dispatch('getTimelogsByCastleAndDate', { castle: this.viewingTimelogCastle, date: this.viewingDate}).then(() => {
        this.isLoadingTimelog = false
      })
    },
    initListeners () {
      this._modalTimelogs = $('#modal-timelogs')

      this._modalTimelogs.on('hidden.bs.modal', () => {
        this.viewingTimelogCastle = ''
      })

      this._modalTimelogs.on('show.bs.modal', () => {
        this.initTimelogModal()
      })
    },
    getBgColorByIndex (index) {
      const availableClass = [
        'bg-success',
        'bg-info',
        'bg-warning',
        'bg-danger',
        'bg-secondary',
        'bg-primary'
      ]

      let resultIndex = index

      if (index >= availableClass.length) {
        resultIndex = index % availableClass.length
      }

      return availableClass[resultIndex]
    },
    castleChangeClickHandler (event, castleName) {
      event.preventDefault()

      this.viewingTimelogCastle = castleName
    }
  },
  updated () {
    if ($('[data-toggle=tooltip]').length > 0) {
      $('[data-toggle=tooltip]').tooltip()
    }
  },
  mounted () {
    this.initListeners()
  }
}
</script>

<style lang="scss" scoped>
    table {
        margin-bottom: 0;
    }

    .btn-sm {
        font-size: 0.675rem;
        line-height: 0.8;
    }

    td {
        /*padding: 0.1rem;*/
        /*font-size: 0.8rem;*/
        vertical-align: middle;
    }

    .progress {
        overflow: visible;

        .progress-bar {
            font-size: 0.1rem;
            position: relative;
        }
        .emblem-container {
            position: absolute;
            top: -2rem;
            left: 50%;
            margin-left: -0.9rem;

            img {
                height: 1.8rem;
                width: 1.8rem;
            }
        }
    }
</style>



