import axios from 'axios'

import * as types from '../mutation-types'

// initial state
const state = {
  guilds: []
}

// getters
const getters = {
  getGuildByName: (state) => (name) => {
    const guild = state.guilds.find(guild => guild.name == name)

    if (guild) {
      return guild
    }

    console.warn(`No data found for guild: ${name}`)
    return {
      name,
      isNoData: true
    }
  }
}

// actions
const actions = {
  getAllGuilds ({ commit }) {
    return axios.get('/static/woe-datas/guild.json')
      .then((response) => {
        commit(types.SET_ALL_GUILDS, { guilds: response.data })
      })
  }
}

// mutations
const mutations = {
  [types.SET_ALL_GUILDS] (state, { guilds }) {
    state.guilds = guilds.map((guild) => {
      guild.emblem = `/static/woe-datas/emblems/${guild.emblem}`

      return guild
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



