<template>
    <div class="card text-center">
        <div class="card-header text-white bg-dark">
            ROMYSG Castle Timelogs as for {{ formattedSelectedDate }}
        </div>
        <div class="card-body">
            <div class="table-responsive">
                <table class="table table-bordered table-sm">
                    <thead>
                    <tr class="">
                        <th scope="col">Castles</th>
                        <th scope="col" class="col-9">Timelogs</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-if="isLoadingTimelog">
                        <td colspan="2">
                            <i class="fa fa-spin fa-spinner"></i>
                        </td>
                    </tr>
                    <tr v-if="!isLoadingTimelog && Object.keys(standingsByDateSelf).length === 0">
                        <td colspan="2">
                            <p class="mb-0">Data is still in progress. Stay tuned </p>
                        </td>
                    </tr>
                    <template v-for="( castles, town ) in standingsByDateSelf" v-if="!isLoadingTimelog && Object.keys(standingsByDateSelf).length > 0">
                        <tr class="table-secondary" :key="town">
                            <th colspan="2">{{ town | capitalize }} Castles</th>
                        </tr>
                        <tr v-for="( castle ) in castles" :key="castle.name">
                            <th scope="row">{{ castle.name }}</th>
                            <td class="">
                                <div class="progress m-2 mt-5">
                                    <template v-for="(row, index) in timelogsByCastleAndDateSelf(castle.name, viewingDate)">
                                        <div :class="['progress-bar', getBgColorByIndex(index, row.inhabitant.name) ]" role="progressbar" :style="{ width: row.percentage + '%'}">
                                            <div class="emblem-container" v-if="row.inhabitant.emblem && (currentViewingGuildName === '' || currentViewingGuildName === row.inhabitant.name)">
                                                <a href="#" @click="guildChooserClickHandler($event, row.inhabitant.name)"><img :src="row.inhabitant.emblem" alt="" class="" data-toggle="tooltip" data-placement="bottom" :title="row.inhabitant.name"></a>
                                            </div>
                                            {{ row.minutesHold }}m
                                        </div>
                                    </template>
                                </div>
                            </td>
                        </tr>
                    </template>
                    </tbody>
                </table>
            </div>

            <!--<h5 class="card-title">Special title treatment</h5>-->
            <!--<p class="card-text">With supporting text below as a natural lead-in to additional content.</p>-->
            <!--<a href="#" class="btn btn-primary">Go somewhere</a>-->
        </div>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import moment from 'moment'

  export default {
    name: 'Timelog',
    data () {
      return {
        isLoadingTimelog: true,
        currentViewingGuildName: ''
      }
    },
    props: [
      'viewingDate'
    ],
    watch: {
      viewingDate (newVal, oldVal) { // watch it
        this.isLoadingTimelog = true
        this.$store.dispatch('getStandingsByDate', newVal).then(() => {
          this.isLoadingStandings = false

          this.$store.dispatch('getTimelogsByCastleAndDate', { castle: this.viewingTimelogCastle, date: this.viewingDate }).then(() => {
            this.isLoadingTimelog = false
          }).catch(() => {
            this.isLoadingTimelog = false
          })
        }).catch(() => {
          this.isLoadingStandings = false
        })
      }
    },
    computed: {
      standingsByDateSelf () {
        return this.standingsByDate(this.viewingDate)
      },
      formattedSelectedDate () {
        return moment(this.viewingDate, 'YYYYMMDD').format('D MMMM YYYY')
      },
      timelogsByCastleAndDateSelf () {
        return (castle) => {
          return this.timelogsByCastleAndDate(castle, this.viewingDate)
        }
      },
      getBgColorByIndex () {
        return (index, guildName) => {
          if (this.currentViewingGuildName !== '') {
            if (this.currentViewingGuildName === guildName) {
              return 'bg-success'
            } else {
              return 'bg-secondary'
            }
          }

          const availableClass = [
            'bg-success',
            'bg-info',
            'bg-warning',
            'bg-danger',
            'bg-secondary',
            'bg-primary'
          ]

          let resultIndex = index

          if (index >= availableClass.length) {
            resultIndex = index % availableClass.length
          }

          return availableClass[resultIndex]
        }
      },
      ...mapGetters([
        'timelogsByCastleAndDate',
        'standingsByDate'
      ])
    },
    methods: {
      guildChooserClickHandler (event, guildName) {
        event.preventDefault()

        if (guildName === this.currentViewingGuildName) {
          this.currentViewingGuildName = ''
          return
        }

        this.currentViewingGuildName = guildName
      }
    },
    updated () {
      if ($('[data-toggle=tooltip]').length > 0) {
        $('[data-toggle=tooltip]').tooltip()
      }
    },
    mounted () {
    }
  }
</script>

<style lang="scss" scoped>
    table {
        margin-bottom: 0;
    }

    .btn-sm {
        font-size: 0.675rem;
        line-height: 0.8;
    }

    td {
        /*padding: 0.1rem;*/
        /*font-size: 0.8rem;*/
        vertical-align: middle;
    }

    .progress {
        overflow: visible;

        .progress-bar {
            font-size: 0.1rem;
            position: relative;
        }
        .emblem-container {
            position: absolute;
            top: -2rem;
            left: 50%;
            margin-left: -0.9rem;

            img {
                height: 1.8rem;
                width: 1.8rem;
            }
        }
    }
</style>



