import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'

import guild from './modules/guilds'
import castles from './modules/castles'
import standings from './modules/standings'
import woe from './modules/woe'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  actions,
  modules: {
    guild,
    castles,
    standings,
    woe
  },
  strict: debug,
  plugins: []
})



