<template>
    <div>
        <div id="app-header" class="container">
            <header class="blog-header py-3">
                <div class="row flex-nowrap justify-content-between align-items-center">
                    <div class="col-12 text-center">
                        <a class="blog-header-logo text-dark" href="#">
                            <img src="../assets/header.gif" class="img-fluid" alt="romysg woe">
                        </a>
                    </div>
                </div>
            </header>
            <div class="row justify-content-center" v-if="getCountdownItem">
                <div class="col-md-8 pt-1 pb-1 text-center">
                    <div class="jumbotron rounded-0 p-4">
                        <h3 v-if="getCountdownItem.type === 'current'">ROMYSG WOE is currently <span class="badge badge-success">running</span></h3>
                        <template v-if="getCountdownItem.type === 'upcoming'">
                            <h3>Upcoming ROMYSG WOE</h3>
                            <p class="mb-0"><strong>Date : </strong>{{ getCountdownItem.date }}</p>
                            <p><strong>Time : </strong>{{ getCountdownItem.start }} - {{ getCountdownItem.end }} (GMT+08)</p>
                        </template>
                        <countdown :time="getCountdownItem.duration" :emit-events="true" @countdownend="handleCountdownEnd">
                            <template slot-scope="props">
                                <span v-if="getCountdownItem.type === 'current'">Time Remaining：</span>
                                <span class="badge badge-info badge-countdown">{{ props.days }}</span> days
                                <span class="badge badge-primary badge-countdown">{{ props.hours }}</span> hours
                                <span class="badge badge-warning badge-countdown">{{ props.minutes }}</span> minutes
                                <span class="badge badge-danger badge-countdown">{{ props.seconds }}</span> seconds
                            </template>
                        </countdown>
                        <!--<h1 class="display-4">Hello, world!</h1>-->
                        <!--<p class="lead">This is a simple hero unit, a simple jumbotron-style component for calling extra attention to featured content or information.</p>-->
                        <!--<hr class="my-4">-->
                        <!--<p>It uses utility classes for typography and spacing to space content out within the larger container.</p>-->
                        <!--<p class="lead">-->
                            <!--<a class="btn btn-primary btn-lg" href="#" role="button">Learn more</a>-->
                        <!--</p>-->
                    </div>
                </div>
            </div>
        </div>
        <nav id="top-navbar" class="navbar navbar-expand-lg navbar-dark bg-dark rounded">
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExample10" aria-controls="navbarsExample10" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse justify-content-md-center" id="navbarsExample10">
                <ul class="navbar-nav">
                    <li :class="{
                      'nav-item': true,
                      'active': this.$route.name === 'PageHome'
                    }">
                        <router-link :to="{ name: 'PageHome' }" class="nav-link px-4">Home</router-link>
                    </li>
                    <li :class="{
                      'nav-item': true,
                      'active': this.$route.name === 'PageTimelogs'
                    }">
                        <router-link :to="{ name: 'PageTimelogs' }" class="nav-link px-4">Timelogs</router-link>
                    </li>
                    <li :class="{
                      'nav-item': true,
                      'active': this.$route.name === 'PageGraphs'
                    }">
                        <router-link :to="{ name: 'PageGraphs' }" class="nav-link px-4">Graphs</router-link>
                    </li>
                    <li :class="{
                      'nav-item': true,
                      'active': this.$route.name === 'PageArchive'
                    }">
                        <router-link :to="{ name: 'PageArchive' }" class="nav-link px-4">Archive</router-link>
                    </li>
                </ul>
            </div>
        </nav>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Topnav',
  computed: {
    ...mapGetters([
      'getCountdownItem'
    ])
  },
  methods: {
    handleCountdownEnd () {
      console.log('==============> countdownend')
      this.$store.dispatch('refreshTimes')
    }
  }
}
</script>

<style lang="scss" scoped>
    .badge-countdown {
        font-size: 1.5rem;
    }
</style>



