import axios from 'axios'
import * as types from '../mutation-types'

// initial state
const state = {
  towns: {}
}

// getters
const getters = {
  castlesPaginatedListByName (state) {
    return (castleName) => {
      let allCastles = []

      for (var index in state.towns) {
        allCastles = [ ...allCastles, ...state.towns[index] ]
      }

      const currentCastleIndex = allCastles.indexOf(castleName)

      const prevIndex = currentCastleIndex !== 0 ? currentCastleIndex - 1 : allCastles.length - 1
      const nextIndex = currentCastleIndex !== allCastles.length - 1 ? currentCastleIndex + 1 : 0
      return {
        prev: allCastles[prevIndex],
        next: allCastles[nextIndex]
      }
    }
  }
}

// actions
const actions = {
  getAllCastles ({ commit }) {
    return axios.get('/static/woe-datas/castles.json')
      .then((response) => {
        commit(types.SET_ALL_CASTLES, { towns: response.data })
      })
  }
}

// mutations
const mutations = {
  [ types.SET_ALL_CASTLES ] (state, { towns }) {
    state.towns = towns
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



