// initial state
import csv from 'csvtojson'
import axios from 'axios/index'
import moment from 'moment'

import * as types from '../mutation-types'
import DateHelper from '../../helpers/date'
import Transformer from '../../helpers/transformer'

const state = {
  standings: {},
  timelogs: {}
}

// getters
const getters = {
  standingsByDate (state, getter, rootState, rootGetters) {
    return (date) => {
      if (!date) {
        if (rootGetters.getLatestWoeTimeWithResult === null) {
          return {}
        }

        date = rootGetters.getLatestWoeTimeWithResult.date
      }

      if (Object.keys(rootState.castles.towns).length === 0 || !state.standings[date] || state.standings[date].length === 0) {
        return {}
      }

      const result = {}

      for (const town in rootState.castles.towns) {
        const castles = rootState.castles.towns[town]

        result[town] = castles.map((castle) => {
          const standing = state.standings[date].find((stand) => stand.castle === castle)

          const latestWoeDate = rootGetters.getLatestWoeTimeWithResult

          const nextWoeFromDate = rootGetters.nextWoeFromDate(date)

          let heldFor = moment(nextWoeFromDate.date).diff(moment(standing['held_for'], 'DD-MM-YYYY'), 'days')

          if (moment(date).diff(moment(latestWoeDate.date)) === 0) {
            heldFor = moment().diff(moment(standing['held_for'], 'DD-MM-YYYY'), 'days')
          }

          return {
            name: castle,
            inhabitants: rootGetters.getGuildByName(standing.inhabitant),
            heldFor,
            breaks: standing.breaks
          }
        })
      }

      return result
    }
  },
  timelogsByCastleAndDate (state, getter, rootState, rootGetters) {
    return (castleName = '', date) => {
      const result = []

      if (castleName === '') {
        return result
      }

      if (typeof date === 'string') {
        date = rootGetters.getWoeTimeByDate(date)
      }

      if (!date) {
        if (rootGetters.getLatestWoeTimeWithResult === null) {
          return result
        }
        date = rootGetters.getLatestWoeTimeWithResult
      }

      const woeStartTime = DateHelper.getWoeMomentFromDate(date.date, date.start)
      const woeEndTime = DateHelper.getWoeMomentFromDate(date.date, date.end)

      let lastLogIndex = 0

      for (var m = woeStartTime.clone().subtract(5, 'm'); m.diff(woeEndTime.clone(), 'minute') <= 0; m.add(1, 'minute')) {
        const dateLog = state.timelogs[date.date]

        if (dateLog) {
          const castleTimelog = dateLog.find((log) => {
            return log.castle === castleName && m.format('HH:mm') === log.time
          })

          if (castleTimelog) {
            result.push({
              time: m.format('LT'),
              inhabitant: rootGetters.getGuildByName(castleTimelog.inhabitant),
              remarks: castleTimelog.remarks,
              minutesHold: 1
            })

            lastLogIndex = result.length - 1
          } else {
            if (woeStartTime.diff(m) !== 0 && result.length > 0) {
              result[lastLogIndex].minutesHold += 1
            }
          }
        }
      }

      let totalPercentage = 0
      for (var i = 0; i < result.length; i++) {
        totalPercentage += result[i].minutesHold
      }

      for (var i = 0; i < result.length; i++) {
        result[i].percentage = ((result[i].minutesHold / totalPercentage) * 100)
      }

      return result
    }
  }
}

// actions
const actions = {
  async getStandingsByDate ({ commit, getters, dispatch }, date) {
    if (!date) {
      if (getters.getLatestWoeTimeWithResult === null) {
        await dispatch('getWoeTimes')
      }

      date = getters.getLatestWoeTimeWithResult.date
    }

    return axios.get(`/static/woe-datas/snapshots/standings/${date}.csv`)
      .then((response) => {
        csv({
          noheader: false,
          headers: [ 'castle', 'inhabitant', 'breaks', 'held_for' ],
          delimiter: ',',
          trim: false
        })
          .fromString(response.data)
          .on('json', (row) => {
            commit(types.ADD_WOE_STANDING, { date, row })
          })
      })
      .catch(() => {
        commit(types.ADD_WOE_STANDING, { date })
      })
  },
  async getTimelogsByCastleAndDate ({ commit, getters, dispatch, state }, { castle, date }) {
    if (!date) {
      if (getters.getLatestWoeTimeWithResult === null) {
        await dispatch('getWoeTimes')
      }

      date = getters.getLatestWoeTimeWithResult.date
    }

    if (state.timelogs[date]) {
      return
    }

    return axios.get(`/static/woe-datas/snapshots/timelogs/${date}.csv`)
      .then((response) => {
        csv({
          noheader: false,
          headers: [ 'time', 'castle', 'inhabitant', 'remarks' ],
          delimiter: ',',
          trim: false
        })
          .fromString(response.data)
          .on('json', (row) => {
            commit(types.ADD_WOE_TIMELOGS, { date, row })
          })
      })
  },
}

// mutations
const mutations = {
  [ types.ADD_WOE_STANDING ] (state, { date, row }) {
    if (!Array.isArray(state.standings[date])) {
      const newObj = {
        [date]: []
      }

      state.standings = {
        ...state.standings,
        ...newObj
      }
    }

    if (row) {
      state.standings[date].push(row)
    }
  },
  [ types.ADD_WOE_TIMELOGS ] (state, { date, row }) {
    if (!Array.isArray(state.timelogs[date])) {
      const newObj = {
        [date]: []
      }

      state.timelogs = {
        ...state.timelogs,
        ...newObj
      }
    }

    if (row) {
      state.timelogs[date].push(row)
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



