import axios from 'axios/index'
import moment from 'moment'

import * as types from '../mutation-types'

const state = {
  dates: []
}

// getters
const getters = {
  nextWoeFromDate (state, getters) {
    return (dateString) => {
      const dates = [ ...state.dates ]

      if (dates.length === 0) {
        return null
      }

      const possibleDates = dates.sort((a, b) => {
        return b.date - a.date
      }).filter((date) => {
        return moment(date.date).isAfter(moment(dateString))
      })

      if (possibleDates.length === 0) {
        return null
      }

      return possibleDates[possibleDates.length -1]
    }
  },
  formattedWoeTimesWithResult (state) {
    const dates = [ ...state.dates ]

    if (dates.length === 0) {
      return []
    }

    const possibleDates = dates.sort((a, b) => {
      return b.date - a.date
    }).filter((date) => {
      return moment(date.date).isSameOrBefore(moment().startOf('day')) && date.hasResult === true
    })

    if (possibleDates.length === 0) {
      return []
    }

    return possibleDates.sort((date, date2) => {
      return moment(date2.date).diff(moment(date.date))
    }).map((date) => {
      return moment(date.date).format('D MMMM YYYY')
    })
  },
  getLatestWoeTimeWithResult (state) {
    const dates = [ ...state.dates ]

    if (dates.length === 0) {
      return null
    }

    const possibleDates = dates.sort((a, b) => {
      return b.date - a.date
    }).filter((date) => {
      return moment(date.date).isSameOrBefore(moment().startOf('day')) && date.hasResult === true
    })

    if (possibleDates.length === 0) {
      return null
    }

    return possibleDates[0]
  },
  getLatestWoeTime (state) {
    const dates = [ ...state.dates ]

    if (dates.length === 0) {
      return null
    }

    const possibleDates = dates.sort((a, b) => {
      return b.date - a.date
    }).filter((date) => {
      return moment(date.date).isSameOrBefore(moment().startOf('day'))
    })

    if (possibleDates.length === 0) {
      return null
    }

    return possibleDates[0]
  },
  getNextWoeTime () {
    const dates = [ ...state.dates ]

    if (dates.length === 0) {
      return null
    }

    const today = moment()

    const possibleDates = dates.sort((a, b) => {
      return a.date - b.date
    }).filter((date) => {
      return getWoeMomentFromDate(date.date, date.start).isAfter(today)
    })

    if (possibleDates.length === 0) {
      return null
    }

    return possibleDates[0]
  },
  getCurrentWoeTime () {
    const now = moment()

    const dates = [ ...state.dates ]

    if (dates.length === 0) {
      return null
    }

    return dates.find((date) => {
      const woeStartTime = getWoeMomentFromDate(date.date, date.start)
      const woeEndTime = getWoeMomentFromDate(date.date, date.end)

      return now.isBetween(woeStartTime, woeEndTime)
    })
  },
  getCountdownItem (state, getters) {
    const now = moment()

    if (getters.getCurrentWoeTime) {
      const date = getters.getCurrentWoeTime
      const woeEndTime = getWoeMomentFromDate(date.date, date.end)

      return {
        type: 'current',
        duration: Math.ceil(moment.duration(woeEndTime.diff(now))),
        date: moment(date.date).format('D MMM YYYY'),
        start: moment(date.start, 'HH:mm:ss').format('LT'),
        end: moment(date.end, 'HH:mm:ss').format('LT')
      }
    }

    if (getters.getNextWoeTime) {
      const date = getters.getNextWoeTime
      const woeStartTime = getWoeMomentFromDate(date.date, date.start)

      return {
        type: 'upcoming',
        duration: Math.ceil(moment.duration(woeStartTime.diff(now))),
        date: moment(date.date).format('D MMM YYYY'),
        start: moment(date.start, 'HH:mm:ss').format('LT'),
        end: moment(date.end, 'HH:mm:ss').format('LT')
      }
    }
  },
  getWoeTimeByDate (state) {
    return (dateString) => {
      return state.dates.find((date) => {
        return moment(date.date).diff(moment(dateString)) === 0
      })
    }
  }
}

// actions
const actions = {
  refreshTimes ({ commit, dispatch }) {
    commit(types.SET_ALL_WOE_TIMES, { dates: [] })

    dispatch('getWoeTimes')
  },
  getWoeTimes ({ commit }) {
    return axios.get('/static/woe-datas/woe-times.json')
      .then((response) => {
        commit(types.SET_ALL_WOE_TIMES, { dates: response.data })
      })
  }
}

// mutations
const mutations = {
  [ types.SET_ALL_WOE_TIMES ] (state, { dates }) {
    state.dates = dates
  }
}

const getWoeMomentFromDate = (date, startTime) => {
  return moment(`${date} ${startTime}`, 'YYYYMMDD HH:mm:ss')
}

export default {
  state,
  getters,
  actions,
  mutations
}



