import Vue from 'vue'
import Router from 'vue-router'
import PageHome from '@/components/PageHome'
import PageTimelogs from '@/components/PageTimelogs'
import PageGraphs from '@/components/PageGraphs'
import PageArchive from '@/components/PageArchive'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'PageHome',
      component: PageHome
    },
    {
      path: '/timelogs',
      name: 'PageTimelogs',
      component: PageTimelogs
    },
    {
      path: '/graphs',
      name: 'PageGraphs',
      component: PageGraphs
    },
    {
      path: '/archive',
      name: 'PageArchive',
      component: PageArchive
    }
  ]
})



